.DetailsCard__body {
  font-family: 'Roboto', sans-serif;
  color: white;
  padding: 0 20px 20px 20px;
  display: grid;
  grid-template-columns: 100%;
  grid-template-areas:
    'main'
    'overview'
    'cast'
    'trailer'
    'recommend'
    'similar'
}

.DetailsCard__body__main {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-area: main;
}

.DetailsCard__body__main_secondary {
  margin-top: 10px;
}

.DetailsCard__body__main_secondary p {
  display: inline-block;
  margin: 20px 20px 0 0;
  margin-right: 20px;
  font-size: 17px;
}

.DetailsCard__body__main_secondary p:first-child {
  display: block;
}

.DetailsCard__body__main_secondary span {
  display: inline-block;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  margin-left: 10px;
  color: #ced3dd;
}

.DetailsCard__body__main__desktop_poster {
  display: none;
}

.DetailsCard__body__main_title,
.DetailsCard__body__title {
  font-weight: 500;
}

.DetailsCard__body__header {
  text-align: left;
  margin: 20px 0;
  margin-top: 40px;
}

.DetailsCard__body__runtime-release {
  margin: 20px 0;
  font-family: 'Open Sans', sans-serif;
}

.DetailsCard__body__runtime {
  margin-right: 30px;
}

.DetailsCard__body__runtime .icon-clock,
.DetailsCard__body__release .icon-calendar {
  display: inline-block;
  position: relative;
  top: 2px;
  margin-right: 10px;
}

.DetailsCard__body__overview {
  text-align: justify;
  grid-area: overview;
}

.DetailsCard__body__overview_p {
  margin: 0;
  max-height: 257px;
  overflow: hidden;
  color: #ced3dd;
}

.ShowFullBioOverview {
  max-height: initial;
}

.DetailsCard__body__cast__list,
.DetailsCard__body__recommend__list,
.DetailsCard__body__similar__list {
  width: 100vw;
  transform: translateX(-20px);
  padding: 20px 20px 15px 20px;
  background-color: #252c3c;
  display: flex;
  align-items: flex-start;
  overflow: auto;
}

.DetailsCard__body__cast__list {
  grid-area: cast;
}

.DetailsCard__body__trailer {
  grid-area: trailer;
}

.DetailsCard__body__recommend {
  grid-area: recommend;
}

.DetailsCard__body__similar {
  grid-area: similar;
}

.iframeTrailer {
  width: 88vw;
  /* max-width: 500px; */
  height: 49.5vw;
  /* max-height: 281px; */
  margin: auto;
  background-color: #141418;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Low Resolution Tablets, Mobiles (Landscape) */
@media (min-width: 481px) and (max-width: 767px) {
  .iframeTrailer {
    width: 70vw;
    height: 39.4vw;
  }
}

/* Tablets, Ipads (landscape) */
@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .iframeTrailer {
    width: 80vw;
    height: 45vw;
  }
}

@media (min-width: 1025px) and (orientation: landscape) {
  .DetailsCard__body {
    grid-column-gap: 20px;
    grid-template-columns: 27% calc(73% - 20px);
    grid-template-areas:
      'main overview'
      'main cast'
      'main trailer'
      'main recommend'
      'main similar'
  }
  .DetailsCard__body__main {
    position: relative;
    top: -150px;
  }
  .DetailsCard__body__main__desktop_poster {
    display: block;
    width: 80%;
    border-radius: 4px;
  }
  .DetailsCard__body__main_secondary {
    align-self: flex-start;
    margin-left: 20px;
    text-align: left;
  }
  .DetailsCard__body__cast__list,
  .DetailsCard__body__recommend__list,
  .DetailsCard__body__similar__list {
    width: 100%;
    transform: translateX(0);
  }
  .iframeTrailer {
    max-width: none;
    max-height: none;
    /* width: 650px;
    height: 366px; */
    width: 100%;
    height: calc(48vw / 1.77);
    /* height: calc(70vw / 1.77); */
  }
}

@media (min-width: 1920px) {
  .DetailsCard__body {
    margin-right: 35px;
  }
  .DetailsCard__body__header {
    font-size: 22px;
  }
  .DetailsCard__body__main {
    padding: 0 30px;
    top: -190px;
  }
  .DetailsCard__body__main_title {
    margin: 30px 0;
    font-size: 1.9em;
  }
  .DetailsCard__body__main__desktop_poster {
    width: 90%;
  }
  .DetailsCard__body__main_secondary {
    margin-top: 20px;
  }
  .DetailsCard__body__runtime-release {
    margin: 30px 0;
  }
  .DetailsCard__body__overview_p,
  .DetailsCard__body__main_secondary p,
  .DetailsCard__body__main_secondary span,
  .DetailsCard__body__runtime span,
  .DetailsCard__body__release span {
    font-size: 20px;
  }
  .DetailsCard__body__runtime .icon-clock,
  .DetailsCard__body__release .icon-calendar {
    font-size: 21px;
  }
  .seasons__label,
  .episodes__label {
    font-size: 19px;
  }
  #seasons,
  #episodes,
  .loadEpisodeButton {
    margin-right: 10px;
    font-size: 18px;
  }
}
